* {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: none;
  /*‚îÄ Debug font size on iphone when landscape orientation ‚îÄ*/
  -webkit-font-smoothing: subpixel-antialiased;
  /*‚îÄ Debug safari font weight when fixed element on the page ‚îÄ*/
  -webkit-font-smoothing: antialiased;
  /*‚îÄ idem : Debug safari font weight when fixed element on the page ‚îÄ*/
}

html,
body,
nav,
header,
footer,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
img,
ul,
li,
table,
form,
label,
input,
textarea,
select,
button {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  //font-family: inherit;
  font-size: 100%;
}

ul {
  list-style: none;
}

li {
  list-style: none;
}

input,
textarea,
select,
button {
  display: block;
  box-sizing: border-box;
  border-radius: 0;
  outline: none;
  border: none;
  background: none;
  color: inherit;
}

input:focus,
textarea:focus,
button:focus {
  outline: none;
}

input[type='submit']:hover,
button:hover {
  cursor: pointer;
}

input::-ms-clear {
  display: none;
  /*‚îÄ IE11 ‚Üí remove cross to close ‚îÄ*/
}

textarea {
  overflow: auto;
  /*‚îÄ Hide scroll bar on IE ‚îÄ*/
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video,
svg,
picture {
  display: block;
}

img,
video {
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: 100%;
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}